        },
        {
          position: 'Estudiante Auxiliar',
          company: 'Universidad Nacional de Colombia',
          description: 'Apoyo en TIC aplicado a la pedagogía. Acompañamiento en formación de habilidades para la escritura y lectura académica de nivel universitario.',
          startDate: 'junio 2015',
          endDate: 'julio 2018'
        },
        {
          position: 'Desarrollador Web',
          company: 'MW moving web',
          description: 'Desarrollador en tecnologías web: php, css, html, javascript. Uso de cms: wordpress.',
          startDate: 'Noviembre 2016',
          endDate: 'Junio 2017'
        },
        {
          position: 'Consultor IT: desarrollador de producto',
          company: 'ANS LTDA',
          description: 'Investigación sobre posibles tecnologías de información para suplir las necesidades de la empresa',
          startDate: 'Diciembre 2015',
          endDate: 'Octubre 2016'
        }
      ]
    }
  },
  mounted () {
    const $ = this.jquery
    this.$nextTick(() => {
      // our custom jQuery code goes here
      $('a.js-scroll-trigger[href*="#"]:not([href="#"])').click(function () {
        if (location.pathname.replace(/^\//, '') === this.pathname.replace(/^\//, '') && location.hostname === this.hostname) {
          var target = $(this.hash)
          target = target.length ? target : $('[name=' + this.hash.slice(1) + ']')
          if (target.length) {
            $('html, body').animate({
              scrollTop: (target.offset().top)
            }, 1000, 'easeInOutExpo')
            return false
          }
        }
      })
      // Closes responsive menu when a scroll trigger link is clicked
      $('.js-scroll-trigger').click(function () {
        $('.navbar-collapse').collapse('hide')
      })
      // Activate scrollspy to add active class to navbar items on scroll
      $('body').scrollspy({
        target: '#sideNav'
      })
    })
  }
}
